import React from "react"
import { Link, graphql, useStaticQuery } from "gatsby"
import Img from "gatsby-image"

const RelatedArticles = props => {
  const data = useStaticQuery(graphql`
    {
      allMarkdownRemark(sort: { fields: [frontmatter___date], order: DESC }) {
        edges {
          node {
            id
            fields {
              slug
            }
            frontmatter {
              tags
              title
              date
              category
              image {
                childImageSharp {
                  fluid(maxWidth: 250, maxHeight: 120) {
                    ...GatsbyImageSharpFluid
                  }
                }
              }
            }
          }
        }
      }
    }
  `)
  const posts = data.allMarkdownRemark.edges

  return (
    <div class="row" dir="rtl">
      {posts
        .filter(
          post =>
            post.node.fields.slug !== props.postSlug &&
            post.node.frontmatter.category.toString() ===
              props.postCat.toString()
        )
        .slice(0, 3)
        .map(post => (
          <div class="col-md-4" key={post.id}>
            <div class="home-article">
              <Link
                to={post.node.fields.slug}
                style={{ textDecoration: "none" }}
              >
                <Img
                  class="articleimg"
                  fluid={post.node.frontmatter.image.childImageSharp.fluid}
                />
                <div class="text-secondary">{post.node.frontmatter.title}</div>
              </Link>

              <div>
                <em>
                  <i class="far fa-calendar-alt"></i>{" "}
                  {post.node.frontmatter.date}
                </em>
                <br />
                <em>
                  <i class="fas fa-clipboard-list"></i>{" "}
                  {post.node.frontmatter.category}
                </em>
              </div>
            </div>
            <br />
          </div>
        ))}
    </div>
  )
}
export default RelatedArticles
