import React from "react"
import { Link, graphql } from "gatsby"
import Layout from "../components/layout"
import MailList from "../components/maillist"
import SEO from "../components/seo"
import Img from "gatsby-image"
import kebabCase from "lodash/kebabCase"
import { DiscussionEmbed } from "disqus-react"
import RelatedArticles from "../components/relatedposts"

export default function Template({ data }) {
  //required variables
  const post = data.markdownRemark
  const category = post.frontmatter.category

  //comments system from disque
  const disqusShortname = "codeitarabic"
  const disqusConfig = {
    identifier: post.id,
    title: post.frontmatter.title,
  }

  return (
    <Layout>
      <SEO title={post.frontmatter.title} description={post.excerpt} />
      <div class="d-flex justify-content-center">
        <div class="col-md-8">
          <br />
          <br />
          <h2>
            <b dir="auto">
              <i class="far fa-bookmark"></i> {post.frontmatter.title}
              <br />
            </b>
          </h2>{" "}
          <Link to={`/category/${kebabCase(category)}`}>
            {" "}
            <span class="badge badge-info">
              {category} <i class="fas fa-clipboard-list"></i>
            </span>
          </Link>{" "}
          <span class="badge badge-info">
            {post.frontmatter.date} <i class="fas fa-clipboard-list"></i>
          </span>
          {"  "}
          <Img
            class="card-img-top"
            fluid={post.frontmatter.image.childImageSharp.fluid}
          />
          <br />
          <div
            dir="auto"
            class="unselectable"
            dangerouslySetInnerHTML={{ __html: post.html }}
          />
          <ul>
            {post.frontmatter.tags.map(tag => (
              <Link to={`/tags/${kebabCase(tag)}`}>
                {" "}
                <span class="badge badge-warning">{tag}</span>
              </Link>
            ))}{" "}
            : <i class="fa fa-tags fa-sm"></i> الوســوم
          </ul>
          <h6>
            تم نشرة في <i class="far fa-calendar-alt"></i> :{" "}
            {post.frontmatter.date}
          </h6>
          <h6>
            <Link to={`/category/${kebabCase(category)}`}>
              {" "}
              <span class="badge badge-info">
                {category} <i class="fas fa-clipboard-list"></i>
              </span>
            </Link>
          </h6>
          <Link to="/" class="btn btn-secondary btn-sm">
            <i class="far fa-arrow-alt-circle-left"></i> العودة للخلف
          </Link>
          <br />
          <br />
          <b>
            {" "}
            : موضوعات ذات صلة <i class="far fa-copy"></i>
          </b>
          <hr />
          <RelatedArticles
            postId={post.id}
            postCat={post.frontmatter.category}
            postSlug={post.fields.slug}
          />
          <hr />
          <div id="content-mobile">
            <div class="p-3 mb-2 bg-success text-white">القائمة البريدية</div>
            <MailList />
          </div>
          <hr />
          <b>
            : أضف تعليقك علي الموضوع <i class="far fa-comment-dots"></i>
          </b>
          <DiscussionEmbed shortname={disqusShortname} config={disqusConfig} />
        </div>
      </div>
    </Layout>
  )
}

export const postQuery = graphql`
  query PostBySlug($slug: String!) {
    markdownRemark(fields: { slug: { eq: $slug } }) {
      html
      id
      fields {
        slug
      }
      excerpt
      frontmatter {
        title
        category
        author
        date
        tags
        image {
          childImageSharp {
            fluid(maxWidth: 750, maxHeight: 300) {
              ...GatsbyImageSharpFluid
            }
          }
        }
      }
    }
  }
`
